@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.rules-container {
  max-width: 80vw;
  margin: 2rem auto;
  border: 2px solid gainsboro;
  padding: 2rem;
  border-radius: 12px;
}

.rules-container .btn.right {
  float: right;
  font-size: 1.5rem;
  padding: .8rem 1.8rem;
}

.card {
  border: 1px solid;
  cursor: pointer;
  margin: 15px;
  position: relative;
  border-radius: 12px;
}

.card-id {
  align-items: center;
  background: #f9e5a9;
  border-radius: 8px;
  display: flex;
  font-size: 1rem;
  height: 1.5rem;
  justify-content: center;
  left: 0;
  margin: 0.2rem;
  position: absolute;
  top: 0;
  width: 1.5rem;
}

.card-img {
  width: 100%;
  border-radius: 12px;
}

.card-img.expanded {
  border: 2px solid;
  left: -50%;
  position: absolute;
  top: -50%;
  width: 200%;
  z-index: 2;
}

.context-menu {
  background-color: white;
  border: 1px solid;
  left: 25%;
  position: absolute;
  top: 15%;
  z-index: 2;
}

.context-menu .menu-action {
  border-bottom: 1px solid;
  margin: 0;
  padding: .7rem 1rem;
  font-weight: 500;
}

.context-menu:last-child {
  border-bottom: none;
}

.context-menu .menu-action:not(.neutral):hover {
  color: white;
  cursor: pointer;
  border-color: black;
}

.overlay {
  border: 4px solid;
  height: 210px;
  left: 0;
  position: absolute;
  top: 0;
  width: 210px;
  border-radius: 12px;
}

.overlay.red {
  background-color: rgba(255, 0, 0, 0.4);
  border-color: rgb(255, 0, 0);
}

.overlay.blue {
  background-color: rgba(0, 149, 255, 0.4);
  border-color: rgb(0, 149, 255);
}

.overlay.neutral {
  background-color: rgba(255, 212, 0, 0.4);
  border-color: rgb(255, 212, 0);
}

.overlay.black {
  background-color: rgba(0, 0, 0, 0.7);
  border-color: rgb(0, 0, 0);
}

.menu-action.red:hover {
  background-color: rgba(255, 0, 0, 0.8);
}

.menu-action.blue:hover {
  background-color: rgba(0, 149, 255, 0.8);
}

.menu-action.neutral:hover {
  background-color: rgba(255, 212, 0, 0.8);
}

.menu-action.black:hover {
  background-color: rgba(0, 0, 0, 0.7);
}

@media only screen and (max-height: 1000px) {
  .overlay {
    height: 180px;
    width: 180px;
  }
}

.container {
  align-items: center;
  display: flex;
  min-height: 100vh;
  justify-content: center;
  width: 100%;
}

.grid {
  display: grid;
  grid-template-columns: 250px 250px 250px 250px 250px;
  grid-template-rows: 250px 250px 250px 250px;
}

.btn-wrapper {
  align-items: flex-end;
  display: flex;
  height: 970px;
}

.btn {
  border-radius: 6px;
  border: 2px solid #00a700;
  color: #000;
  color: initial;
  cursor: pointer;
  display: block;
  font-size: 1rem;
  font-weight: 500;
  margin-top: 1rem;
  padding: 0.5rem 1rem;
  text-decoration: none;
  transition: color .15s ease-in-out, background-color .15s ease-in-out, border-color .15s ease-in-out, box-shadow .15s ease-in-out;
}

.btn:hover {
  background-color: #00a700;
  border-color: #009200;
  color: white;
}

.btn.blue {
  border-color: #3a3aff;
}

.btn.blue:hover {
  background-color: #3a3aff;
  border-color: blue;
}

@media only screen and (max-height: 1000px) {
  .grid {
    display: grid;
    grid-template-columns: 220px 220px 220px 220px 220px;
    grid-template-rows: 220px 220px 220px 220px;
  }

  .btn-wrapper {
    height: 850px;
  }
}

@media only screen and (max-width: 1300px) {
  .grid {
    display: grid;
    grid-template-columns: 18vw 18vw 18vw 18vw 18vw;
    grid-template-rows: 18vw 18vw 18vw 18vw;
  }

  .container {
    flex-direction: column;
  }

  .btn-wrapper {
    height: auto;
    margin-bottom: 1rem;
  }

  .btn {
    display: inline-block;
    margin: 1rem 1rem 1rem 0;
  }
}

.find-card-wrapper {
  text-align: center;
}

.input {
  padding: 0.5rem 1rem;
  border: 2px solid #c1c1c1;
  border-radius: 4px;
  font-size: 1rem;
}

.find-card-wrapper .btn {
  display: inline-block;
  margin-left: 1rem;
}

.container.spy-master {
  flex-direction: column;
  min-height: 80vh;
}

.card-container {
  display: flex;
  align-items: center;
}

.title {
  font-size: 3.5rem;
}

.spy-grid {
  background-color: black;
  border-radius: 12px;
  display: grid;
  grid-template-columns: 150px 150px 150px 150px 150px;
  grid-template-rows: 125px 125px 125px 125px;
  padding: 15px;
}

.spy-grid .card {
  border-radius: 12px;
  margin: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.spy-grid .card.red {
  background: rgb(255,77,94);
  background: radial-gradient(circle, rgba(255,77,94,1) 0%, rgba(201,34,50,1) 100%);
}

.spy-grid .card.blue {
  background: rgb(72,168,255);
  background: radial-gradient(circle, rgba(72,168,255,1) 0%, rgba(72,122,167,1) 100%);
}

.spy-grid .card.neutral {
  background-color: #daca9e;
}

.spy-grid .card.black {
  background: rgb(99,99,99);
  background: radial-gradient(circle, rgba(99,99,99,1) 0%, rgba(62,60,58,1) 100%);
}

.spy-grid .card::before {
  font-weight: 900;
  font-size: 3rem;
}

.spy-grid .card.black::before {
  content: '✕';
  text-shadow: 0 0 5px #FFF, 0 0 10px #FFF, 0 0 15px #FFF, 0 0 20px #b9b9b9, 0 0 30px #b9b9b9, 0 0 40px #b9b9b9, 0 0 55px #b9b9b9, 0 0 75px #b9b9b9;
}

.spy-grid .card.red::before {
  content: '◇';
  color: #f73434;
  font-size: 5rem;
  text-shadow: 0 0 5px #FFF, 0 0 10px #FFF, 0 0 15px #FFF, 0 0 20px #ff1818, 0 0 30px #ff1818, 0 0 40px #ff1818, 0 0 55px #ff1818, 0 0 75px #ff1818;
}

.spy-grid .card.blue::before {
  content: '○';
  color: #0088ff;
  font-size: 5rem;
  text-shadow: 0 0 5px #FFF, 0 0 10px #FFF, 0 0 15px #FFF, 0 0 20px #0088ff, 0 0 30px #0088ff, 0 0 40px #0088ff, 0 0 55px #0088ff, 0 0 75px #0088ff;
}

.starting-color {
  border-radius: 8px;
  height: 100px;
  margin: 15px;
  width: 30px;
}

.starting-color.red {
  background: rgb(255,77,94);
  background: radial-gradient(circle, rgba(255,77,94,1) 0%, rgba(201,34,50,1) 100%);
  box-shadow: 0px 0px 21px 2px rgba(255,77,94,1);
  -moz-box-shadow: 0px 0px 21px 2px rgba(255,77,94,1);
  -webkit-box-shadow: 0px 0px 21px 2px rgba(255,77,94,1);
}

.starting-color.blue {
  background: rgb(72,168,255);
  background: radial-gradient(circle, rgba(72,168,255,1) 0%, rgba(72,122,167,1) 100%);
  box-shadow: 0px 0px 21px 2px rgba(72,168,255,1);
  -moz-box-shadow: 0px 0px 21px 2px rgba(72,168,255,1);
  -webkit-box-shadow: 0px 0px 21px 2px rgba(72,168,255,1);
}


@media only screen and (max-width: 900px) {
  .title {
    font-size: 2.5rem;
  }

  .spy-grid .card::before {
    font-size: 2rem;
  }

  .spy-grid .card.red::before, .spy-grid .card.blue::before {
    font-size: 2.5rem;
  }

  .spy-grid {
    grid-template-columns: 100px 100px 100px 100px 100px;
    grid-template-rows: 75px 75px 75px 75px;
  }
}


@media only screen and (max-width: 650px) {
  .spy-grid {
    grid-template-columns: 75px 75px 75px 75px 75px;
    grid-template-rows: 55px 55px 55px 55px;
  }
  .spy-grid .card::before {
    font-size: 0.7rem;
  }
  .spy-grid .card.blue::before, .spy-grid .card.red::before {
    font-size: 1rem;
  }
  .starting-color {
    height: 60px;
    margin: 10px;
    width: 20px;
  }
}


@media only screen and (max-width: 525px) {
  .title {
    font-size: 2rem;
  }
  .spy-grid {
    grid-template-columns: 50px 50px 50px 50px 50px;
    grid-template-rows: 40px 40px 40px 40px;
  }
}

body {
  font-family: 'Roboto', sans-serif;
}

